.page
{
    margin: 10px;
}

.title
{
    text-align: center;
}

.interactions
{
    text-align: center;
    padding: 10px;
}

.searchTitle
{
    font-weight: bold;
}

.searchForm
{
    padding-top: 5px;
    width: 100%;
}

.searchElem
{
    text-align: right;
}

.searchField
{
    margin: 5px;
}


.dataContainer
{
    margin: auto;
}

.listHolder
{
    float: left;
    width: 80%;
}

.table-holder
{
    padding: 1px;
    width: 100%;
}

.table
{
    margin: 5px 0;
    height: 500px;
    overflow: scroll;
}

.table-header
{
    display: -ms-flexbox;
    display: flex;
    line-height: 24px;
    padding: 0 10px;
    font-weight: bold;
}

.table-empty
{
    margin: 200px;
    text-align: center;
    font-size: 16px;
}

.table-row
{
    display: -ms-flexbox;
    display: flex;
    line-height: 24px;
    white-space: nowrap;
    margin: 10px 0;
    padding: 10px;
    background: #ffffff;
    border: 1px solid #e3e3e3;
}

.table-header > span
{
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding: 0 5px;
}

.table-row > span
{
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding: 0 5px;
}

.button-hidden
{
    display: none;
}
.button-inline
{
    border-width: 1px;
    background: transparent;
    color: inherit;
    text-align: inherit;
    -webkit-font-smoothing: inherit;
    padding: 1px;
    font-size: inherit;
    cursor: pointer;
}

.button-active
{
    border-radius: 0;
    border-bottom: 1px solid #38BB6C;
}


